import React from "react";

export const Features = (props) => {
  return (
    <div id="features" className="text-center features">
    <div className="container">
      <div className="section-title">
        <h2 className="text-white mt-5">Features ()</h2>
      </div>
      <div className="row">
        {props.data ? props.data.map((d, i) => {
              return (
                <div key={`${d.title}-${i}`} className="cardft col-12 col-md-9 col-lg-2 m-3 p-2 rounded-full  " // 
                  style={{ width: ""  }}  >
                  {/*  icon */}
                  {/* <img src="..." className="card-img-top" alt="..."></img> */}
                  <div className="card-body">
                  <i style={{text:"center"}} className={d.icon}></i>
                    <h3 className="card-title text-primary ">{d.title}</h3>
                    <p className="card-text text">{d.text}</p>
                    {/* <a href="#" className="btn btn-primary">
                      Go somewhere
                    </a> */}
                  </div>
                </div>
              );
            })
          : "Loading..."}
      </div>
    </div>
  </div>
    // <div className="card container" style={{width:"100px"}}>
    //   <img src="" className="card-img-top" alt="" />
    //   <div className="card-body">
    //     <h5 className="card-title">ssss</h5>
    //     <p className="card-text">sssssss</p>
    //     <a className="btn btn-primary">xxxx</a>
    //   </div>

    // </div>
  );
};

